/**
 * ErrorService - Сервис для обработки и мониторинга ошибок
 */
import * as Sentry from '@sentry/react';
import { browserTracingIntegration } from '@sentry/browser';
import { replayIntegration } from '@sentry/browser';

class ErrorService {
  /**
   * Конструктор сервиса ошибок
   */
  constructor() {
    this.isInitialized = false;
    this.sentryDsn = 'https://160bcb3a99ff8a5a2fdcf0a12218de39@o4509191639597056.ingest.de.sentry.io/4509192317370448';
    this.environment = process.env.NODE_ENV || 'development';
    this.version = process.env.APP_VERSION || '1.0.0';
  }

  /**
   * Инициализация сервиса
   * @param {Object} telegramUser - Информация о пользователе Telegram (опционально)
   */
  init(telegramUser = null) {
    // Если сервис уже инициализирован, выходим
    if (this.isInitialized) {
      return false;
    }

    try {
      // Инициализация Sentry
      Sentry.init({
        dsn: this.sentryDsn,
        integrations: [
          browserTracingIntegration({
            tracingOrigins: ['localhost', 'criminal-bluff-production.up.railway.app'],
          }),
          replayIntegration({
            // Настройки для Session Replay
            maskAllText: true,
            blockAllMedia: true,
          }),
        ],
        // Установка процента выборки для трассировки и сессий
        tracesSampleRate: this.environment === 'production' ? 0.1 : 1.0,
        replaysSessionSampleRate: 0.1, // Записывать 10% сессий
        replaysOnErrorSampleRate: 1.0, // Записывать все сессии с ошибками
        
        environment: this.environment,
        release: this.version,
        
        // Фильтрация неважных ошибок
        beforeSend(event, hint) {
          const error = hint && hint.originalException;
          
          // Добавляем тип ошибки в теги
          if (error && error.name) {
            event.tags = event.tags || {};
            event.tags.error_type = error.name;
          }
          
          // Игнорируем ошибки CORS и ошибки от расширений браузера
          if (event.message && (
            event.message.includes('Cross-Origin Request Blocked') ||
            event.message.includes('Extension') ||
            event.message.includes('ResizeObserver loop') ||
            event.message.includes('Script error')
          )) {
            return null;
          }
          
          // Исключаем некоторые URL из трассировки
          if (event.request && event.request.url) {
            const url = event.request.url;
            if (url.includes('chrome-extension://') || 
                url.includes('moz-extension://')) {
              return null;
            }
          }
          
          return event;
        }
      });
      
      // Если передан пользователь Telegram, устанавливаем контекст пользователя
      if (telegramUser) {
        this.setUser(telegramUser);
      }
      
      // Добавляем глобальный обработчик необработанных ошибок
      window.addEventListener('error', (event) => {
        this.captureException(event.error || new Error(event.message), {
          tags: { mechanism: 'onerror' }
        });
      });
      
      // Добавляем глобальный обработчик необработанных промисов
      window.addEventListener('unhandledrejection', (event) => {
        this.captureException(event.reason || new Error('Unhandled Promise rejection'), {
          tags: { mechanism: 'onunhandledrejection' }
        });
      });
      
      this.isInitialized = true;
      console.log('Sentry initialized successfully');
      return true;
    } catch (error) {
      console.error('Error initializing Sentry:', error);
      return false;
    }
  }
  
  /**
   * Установка информации о пользователе
   * @param {Object} user - Информация о пользователе
   */
  setUser(user) {
    if (!this.isInitialized || !user) {
      return;
    }
    
    try {
      Sentry.setUser({
        id: user.telegramId || user.id,
        username: user.username,
        name: `${user.firstName || ''} ${user.lastName || ''}`.trim(),
        ip_address: '{{auto}}'
      });
    } catch (error) {
      console.error('Error setting user context:', error);
    }
  }
  
  /**
   * Очистка информации о пользователе
   */
  clearUser() {
    if (!this.isInitialized) {
      return;
    }
    
    try {
      Sentry.setUser(null);
    } catch (error) {
      console.error('Error clearing user context:', error);
    }
  }
  
  /**
   * Отправка ошибки в Sentry
   * @param {Error} error - Объект ошибки
   * @param {Object} [additionalData={}] - Дополнительная информация
   */
  captureException(error, additionalData = {}) {
    if (!this.isInitialized) {
      console.error(error);
      return;
    }
    
    try {
      Sentry.withScope(scope => {
        // Добавляем дополнительную информацию
        if (additionalData.tags) {
          Object.entries(additionalData.tags).forEach(([key, value]) => {
            scope.setTag(key, value);
          });
        }
        
        if (additionalData.extra) {
          Object.entries(additionalData.extra).forEach(([key, value]) => {
            scope.setExtra(key, value);
          });
        }
        
        if (additionalData.level) {
          scope.setLevel(additionalData.level);
        }
        
        // Добавляем данные о Telegram WebApp, если доступны
        if (window.Telegram?.WebApp) {
          scope.setTag('telegram_platform', window.Telegram.WebApp.platform || 'unknown');
          scope.setTag('telegram_version', window.Telegram.WebApp.version || 'unknown');
          scope.setContext('Telegram WebApp', {
            colorScheme: window.Telegram.WebApp.colorScheme,
            viewportHeight: window.Telegram.WebApp.viewportHeight,
            viewportStableHeight: window.Telegram.WebApp.viewportStableHeight,
            initDataUnsafe: !!window.Telegram.WebApp.initDataUnsafe
          });
        }
        
        // Отправляем ошибку
        Sentry.captureException(error);
      });
    } catch (captureError) {
      console.error('Error sending exception to Sentry:', captureError);
      console.error('Original error:', error);
    }
  }
  
  /**
   * Отправка сообщения в Sentry
   * @param {String} message - Текст сообщения
   * @param {String} [level='info'] - Уровень сообщения ('info', 'warning', 'error')
   */
  captureMessage(message, level = 'info') {
    if (!this.isInitialized) {
      console.log(message);
      return;
    }
    
    try {
      Sentry.captureMessage(message, level);
    } catch (error) {
      console.error('Error sending message to Sentry:', error);
    }
  }
  
  /**
   * Создание и возврат транзакции
   * @param {Object} options - Параметры транзакции
   * @param {String} options.name - Имя транзакции
   * @param {String} options.op - Тип операции
   * @param {Object} [options.data={}] - Дополнительные данные
   * @returns {Object|null} - Объект транзакции или null
   */
  startTransaction(options) {
    if (!this.isInitialized) {
      // Возвращаем объект-заглушку, чтобы не вызывать ошибки при использовании
      return {
        finish: () => {},
        setStatus: () => {},
        startChild: () => ({ finish: () => {} })
      };
    }
    
    try {
      // Проверяем доступность Sentry.startTransaction
      if (typeof Sentry.startTransaction === 'function') {
        const transaction = Sentry.startTransaction({
          name: options.name,
          op: options.op,
          data: options.data || {}
        });
        return transaction;
      } else {
        console.warn('Sentry.startTransaction is not available, using stub');
        // Возвращаем объект-заглушку, если функция недоступна
        return {
          finish: () => {},
          setStatus: () => {},
          startChild: () => ({ finish: () => {} })
        };
      }
    } catch (error) {
      console.error('Error starting transaction:', error);
      // Возвращаем объект-заглушку, чтобы не вызывать ошибки при использовании
      return {
        finish: () => {},
        setStatus: () => {},
        startChild: () => ({ finish: () => {} })
      };
    }
  }
}

// Экспортируем инстанс сервиса
export default new ErrorService(); 